<template>
  <div class="container">
    <div class="header">
      <span style="font-size: 12px; color: pink"
        >说明：目前支持学科和关键字条件筛选</span
      >
      <el-button
        type="success"
        icon="el-icon-edit"
        size="small"
        style="float: right"
        @click="$router.push('new')"
        >新增试题</el-button
      >
    </div>
    <el-form :model="form" label-width="85px" :inline="true">
      <el-row>
        <el-form-item label="学科" class="el-col el-col-6">
          <!-- <el-input  size="small"  ></el-input> -->
          <el-select
            v-model="form.subjectID"
            size="small"
            placeholder="请选择"
            @change="submit"
          >
            <el-option
              v-for="item in subjects"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="二级目录" class="el-col el-col-6">
          <!-- <el-input size="small" ></el-input> -->
          <el-select v-model="form.catalogID" size="small" placeholder="请选择">
            <el-option
              v-for="item in list"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="标签" class="el-col el-col-6">
          <el-select v-model="form.tags" size="small" placeholder="请选择">
            <el-option
              v-for="item in tagOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="关键字" class="el-col el-col-6">
          <el-input
            v-model="form.keyword"
            placeholder="根据题干搜索"
            size="small"
          ></el-input>
        </el-form-item>
        <el-form-item label="试题类型" class="el-col el-col-6">
          <el-select
            v-model="form.questionType"
            size="small"
            placeholder="请选择"
          >
            <el-option
              v-for="item in questionType"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="难度" class="el-col el-col-6">
          <el-select
            v-model="form.difficulty"
            size="small"
            placeholder="请选择"
          >
            <el-option
              v-for="item in difficulty"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="方向" class="el-col el-col-6">
          <el-select v-model="form.direction" size="small" placeholder="请选择">
            <el-option
              v-for="item in direction"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="录入人" class="el-col el-col-6">
          <el-select v-model="form.creatorID" size="small" placeholder="请选择">
            <el-option
              v-for="item in user"
              :key="item.id"
              :label="item.username"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="题目备注" class="el-col el-col-6">
          <el-input v-model="form.remarks" size="small"></el-input>
        </el-form-item>
        <el-form-item label="企业简称" class="el-col el-col-6">
          <el-input v-model="form.shortName" size="small"></el-input>
        </el-form-item>
        <el-form-item label="城市" class="el-col el-col-6">
          <el-select
            v-model="form.province"
            size="small"
            placeholder="请选择"
            style="width: 49%"
            @change="handleCity"
          >
            <el-option
              v-for="item in cityOptions"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
          <el-select
            v-model="form.city"
            size="small"
            placeholder="请选择"
            style="width: 49%; margin-left: 2%"
          >
            <el-option
              v-for="item in city"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="el-col el-col-6" style="text-align: right">
          <el-button size="small" @click="clear">清除</el-button>
          <el-button
            type="primary"
            style="margin-left: 10px"
            size="small"
            @click="submit1"
            >搜索</el-button
          >
        </el-form-item>
      </el-row>
    </el-form>
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane label="全部" name="first"></el-tab-pane>
      <el-tab-pane label="待审核" name="second"></el-tab-pane>
      <el-tab-pane label="已审核" name="third"></el-tab-pane>
      <el-tab-pane label="已拒绝" name="fourth"></el-tab-pane>
    </el-tabs>
    <el-alert
      :title="`数据一共${count}条`"
      type="info"
      :closable="false"
      show-icon
      style="margin-bottom: 15px"
    >
    </el-alert>
    <el-table
      :data="tableData"
      style="width: 100%"
      :header-cell-style="getRowClass"
    >
      <el-table-column
        prop="number"
        label="试题编号"
        width="230"
      ></el-table-column>
      <el-table-column
        prop="subject"
        label="学科"
      ></el-table-column>
      <el-table-column
        prop="catalog"
        label="目录"
      ></el-table-column>
      <el-table-column prop="questionType" label="题型">
        <template slot-scope="{ row }">
          {{
            row.questionType === "1"
              ? "单选"
              : row.questionType === "2"
              ? "多选"
              : "简答"
          }}
        </template>
      </el-table-column>
      <el-table-column prop="question" label="题干" width="280">
        <template slot-scope="{ row }">
          <div v-html="row.question"></div>
        </template>
      </el-table-column>
      <el-table-column prop="addDate" label="录入时间" width="180">
        <template slot-scope="{ row }">
          {{ row.addDate | parseTimeByString }}
        </template>
      </el-table-column>
      <el-table-column prop="difficulty" label="难度" width="80">
        <template slot-scope="{ row }">
          {{
            row.difficulty === "1"
              ? "简单"
              : row.difficulty === "2"
              ? "一般"
              : "困难"
          }}
        </template>
      </el-table-column>
      <el-table-column
        prop="creator"
        label="录入人"
        width="120"
      ></el-table-column>
      <el-table-column prop="chkState" label="审核状态" width="120">
        <template slot-scope="{ row }">
          {{
            row.chkState === 0
              ? "待审核"
              : row.chkState === 1
              ? "已审核"
              : "已拒绝"
          }}
        </template>
      </el-table-column>
      <el-table-column
        prop="chkRemarks"
        label="审核意见"
        width="180"
      ></el-table-column>
      <el-table-column
        prop="chkUser"
        label="审核人"
        width="120"
      ></el-table-column>
      <el-table-column prop="publishState" label="发布状态" width="150">
        <template slot-scope="{ row }">
          {{ row | state_pub }}
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="操作" width="200">
        <template slot-scope="{ row }">
          <el-button type="text" size="small" @click="look(row)"
            >预览</el-button
          >
          <el-button
            type="text"
            size="small"
            :disabled="row.chkState === 1 || row.chkState === 2"
            @click="open(row)"
            >审核</el-button
          >
          <el-button
            type="text"
            size="small"
            :disabled="row.publishState === 1"
            @click="$router.push(`new?id=${row.id}`)"
            >修改</el-button
          >
          <el-button type="text" size="small" @click="open1(row)">{{
            row.publishState === 0 ? "上架" : "下架"
          }}</el-button>
          <el-button
            type="text"
            size="small"
            :disabled="row.publishState === 1"
            @click="del(row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      style="text-align: right; margin-top: 20px"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :page-sizes="[5, 10, 20, 50]"
      :page-size="5"
      background
      layout=" prev, pager, next,sizes, jumper"
      :total="count"
      hide-on-single-page
    >
    </el-pagination>
    <!-- 审核 -->
    <el-dialog
      title="题目审核"
      :visible.sync="dialogVisible"
      width="400px"
      :before-close="handleClose"
    >
      <el-form ref="formdata" :model="formdata">
        <el-form-item>
          <el-radio v-model="formdata.chkState" :label="1">通过</el-radio>
          <el-radio v-model="formdata.chkState" :label="2">拒绝</el-radio>
        </el-form-item>
        <el-form-item>
          <el-input
            type="textarea"
            :rows="2"
            placeholder="请输入审核意见"
            v-model="formdata.chkRemarks"
          >
          </el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="confirm">确 定</el-button>
      </span>
    </el-dialog>
    <Container ref="questionPreview" :data="questionInfo"></Container>
  </div>
</template>

<script>
import { simple } from '@/api/hmmm/subjects'
import { simple as directoryList } from '@/api/hmmm/directorys'
import { simple as tagList } from '@/api/hmmm/tags'
import { list as list1 } from '@/api/base/users'
import { questionType, difficulty, direction } from '@/api/hmmm/constants'
import {
  choice,
  choiceCheck,
  choicePublish,
  remove
} from '@/api/hmmm/questions'
import { provinces, citys } from '@/api/hmmm/citys'
export default {
  data () {
    return {
      questionInfo: {},
      dialogVisible: false,
      formdata: {
        id: null,
        chkState: 1, // 审核状态
        chkRemarks: null // 审核内容
      },
      activeName: 'first',
      form: {
        page: 1,
        pagesize: 5,
        catalogID: null,
        tags: null,
        city: null,
        chkState: null
      },
      count: 0,
      subjects: [],
      list: [], // 目录
      tagOptions: [], // 标签
      questionType: [], // 试题类型
      difficulty: [], // 难度
      direction: [], // 方向
      user: [], // 录入人
      tableData: [],
      cityOptions: provinces(),
      city: []
    }
  },
  async created () {
    const res = await simple()
    this.subjects = res.data
    const data = await questionType
    this.questionType = data
    const data1 = await difficulty
    this.difficulty = data1
    const data2 = await direction
    this.direction = data2
    const data3 = await list1()
    this.user = data3.data.list
    this.data()
  },
  methods: {
    async data () {
      const res = await choice(this.form)
      this.tableData = res.data.items
      this.count = res.data.counts
      // console.log(res)
    },
    async open (row) {
      this.dialogVisible = true
      this.formdata.id = row.id
      // console.log(row)
    },
    // 审核确定
    async confirm () {
      if (this.formdata.chkRemarks === null) {
        return this.$message.warning('请输入审核意见')
      }
      await choiceCheck(this.formdata)
      // console.log(res)
      this.handleClose()
      this.$message.success('操作成功')
      this.data()
    },
    // 审核取消
    handleClose () {
      this.dialogVisible = false
      this.formdata.chkRemarks = null
    },
    // 上架 / 下架
    async open1 (row) {
      await this.$confirm(
        `您确认${row.publishState === 0 ? '上架' : '下架'}这道题目吗?`,
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      )
      await choicePublish({
        id: row.id,
        publishState: row.publishState === 1 ? 0 : 1
      })
      this.$message.success(`${row.publishState === 0 ? '上架' : '下架'}成功`)
      this.data()
    },
    // 删除
    async del (row) {
      await this.$confirm('此操作将永久删除该题目, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
      await remove(row)
      this.data()
    },
    // 预览
    look (row) {
      this.questionInfo = row
      this.$nextTick(() => {
        this.$refs.questionPreview.open()
      })
    },
    // 二级联动
    async submit (subjectID) {
      this.form.catalogID = null
      this.form.tags = null
      if (subjectID) {
        // 获取目录
        const { data: directoryArr } = await directoryList({ subjectID })
        this.list = directoryArr
        // console.log(directoryArr)
        // 标签下拉选项
        const { data: tagArr } = await tagList({ subjectID })
        this.tagOptions = tagArr
      } else {
        this.list = []
        this.tagOptions = []
      }
    },
    handleCity (city) {
      // 地区选中的数据清空
      this.form.city = null
      // 获取城市下的地区赋值给areaOptions
      this.city = citys(city)
    },
    // 清除
    clear () {
      this.form = {
        page: 1,
        pagesize: 5
      }
      this.data()
    },
    // 搜索
    submit1 () {
      this.data(this.form)
    },
    // 切换
    handleClick (tab, event) {
      // console.log(tab.label)
      if (tab.label === '全部') {
        this.form.chkState = null
        this.data()
      } else if (tab.label === '待审核') {
        this.form.chkState = 0
        this.data()
      } else if (tab.label === '已审核') {
        this.form.chkState = 1
        this.data()
      } else if (tab.label === '已拒绝') {
        this.form.chkState = 2
        this.data()
      }
    },
    // 改变表头样式
    getRowClass ({ rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        return 'background:#f8faff; border-bottom: 2px solid #e8e8e8;'
      }
    },
    handleSizeChange (val) {
      // console.log(`每页 ${val} 条`)
      this.form.pagesize = val
      this.form.page = 1
      this.data()
    },
    handleCurrentChange (val) {
      // console.log(`当前页: ${val}`)
      this.form.page = val
      this.data()
    }
  },
  filters: {
    state_pub (row) {
      if (row.publishState === 1 && row.chkState === 1) {
        return '已发布'
      }
      if (row.publishState === 0 && row.chkState === 1) {
        return '已下架'
      }
      return '待发布'
    }
  }
}
</script>

<style scoped lang="scss">
.container {
  // width: 1242px;
  padding: 20px;
  margin: 10px;
  background-color: #fff;
}
.header {
  height: 32px;
  margin-bottom: 15px;
}
.el-select {
  width: 100%;
}

.el-col {
  float: left;
}
::v-deep .el-form-item__content {
  flex: 1;
}
.el-form-item {
  display: flex;
  margin-right: 0;
}
</style>
